@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
div#root {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: white;
  font-family: "Oswald";
}
div.App {
  height: 100%;
  width: 100%;
}

ul.nav {
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  height: 400px;
  width: 100%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  list-style-type: none;
  z-index: 2;
}

ul.nav li {
  margin: 25px 30px;
  width: 200px;
}
ul.nav li a {
  display: block;
  color: black;
  font-size: 45px;
  /* font-family: "Josefin Sans", sans-serif; */
  font-family: "Raleway";
  font-family: "Bebas Neue", cursive;
  text-decoration: none;
  text-align: center;
  text-transform: capitalize;
  transition: 0.6s;
  position: relative;
  padding-bottom: 3px;
  /* letter-spacing: 2px; */
  opacity: 0.4;
}

ul.nav li:nth-last-of-type(1) {
  margin-right: 150px;
}

ul.nav li a:hover {
  opacity: 1;
}

ul.nav li a.active {
  opacity: 1;
  letter-spacing: 3px;
}

div.pages {
  padding: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
  width: 70%;
  max-height: 90%;
  background-color: black;
}
.pages h1 {
  font-size: 110px;
  letter-spacing: 2px;
  font-family: "Raleway";
  margin-bottom: 50px;
}
.pages p {
  font-family: "Lato";
  font-size: 25px;
  letter-spacing: 1px;
}

/* @import url("https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@600&display=swap"); */
.homepage {
  color: white;
}
.logo {
  font-size: 170px;
  font-family: "Bebas Neue", cursive;
  position: absolute;
  bottom: 5%;
  right: 5%;
  z-index: 1;
  color: black;
}
.logo2 {
  position: absolute;
  top: 10px;
  left: 15px;
  font-size: 40px;
  font-family: "Bebas Neue", cursive;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: black;
}
img {
  height: 70%;
  position: absolute;
  bottom: 0;
  left: 200px;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

div.text {
  padding: 50px;
  position: absolute;
  top: 15%;
  right: 100px;
  width: 50%;
  background-color: black;
  font-family: "Lato";
  font-size: 25px;
  letter-spacing: 1px;
}
div.text h1 {
  padding: 10px 0;
  font-size: 65px;
  font-family: "Raleway";
}

